import Header from "./component/Header";
import Bg from "./component/Bg";
import Pic from './component/Pic';
import Go from './component/Go';
import Card from './component/Card';
import Cart from './component/Cart'
import Detail from "./component/Detail"
import data from "./component/data";
import "./App.css";
import axios from 'axios';
import { useState, useEffect } from "react";
import { Routes, Route, useNavigate, Outlet } from 'react-router-dom'




function App() {

  // let obj = { name: 'kim' }
  // localStorage.setItem('data', JSON.stringify(obj))
  // let  = localStorage.getItem('data')
  // console.log(JSON.parse());
  useEffect(() => {
    localStorage.setItem('watched', JSON.stringify([]))
  }, [])

  let [items, itemss] = useState(data);
  let [button, setButton] = useState(1);
  let [fade, setFade] = useState("");

  useEffect(() => {
    setTimeout(() => {
      setFade("end");
    }, 100);
    return () => {
      setFade("");
    };
  }, [App]);

  let buttonClick = (() => {
    if (button === 1) {
      axios.get('https://ttochino.github.io/data1.json')
        .then((data) => {
          let copy = [...items, ...data.data];
          itemss(copy);
        })
        .catch(() => {
          console.log("  .")
        })
    } else if (button === 2) {
      axios.get('https://ttochino.github.io/data2.json')
        .then((data) => {
          let copy = [...items, ...data.data];
          itemss(copy);
        })
        .catch(() => {
          console.log("  .")
        })
    } else {
      alert("   .")
      { <div> appp.style.visibility = 'hidden' </div> };
    }
  })

  return (
    <div className={"start " + fade}>
      <Header />
      <Routes>
        <Route path="/" element={
          <>
            <Bg />
            <div className='App'>
              <button className='bt bt1'
                onClick={() => {
                  let copy = [...items];
                  itemss(copy.sort((a, b) => a.id - b.id))
                }}>
                 
              </button>
              <button className='bt bt1'
                onClick={() => {
                  let copy = [...items];
                  itemss(copy.sort((a, b) => a.title.localeCompare(b.title)))
                }}>
                 
              </button>
              <button className='bt bt1'
                onClick={() => {
                  let copy = [...items];
                  itemss(copy.sort((a, b) => a.price - b.price))
                }}>
                 
              </button>
            </div>
            <div className="container">
              {items.map(function (a, i) {
                return (
                  <Card items={items[i]} i={i + 1} />
                );
              })}
            </div>
            <div className='App'
              onClick={buttonClick}>
              <button className="bt bt1"
                onClick={() => {
                  setButton(button + 1)
                  console.log(button)
                }}
              > 
              </button>
            </div>
          </>} />
        <Route path="/detail/:id" element={<Detail items={items} />} />
        <Route path="/cart" element={<Cart />} />
        <Route path="/about" element={<About />} >
          <Route path="pic" element={<Pic />} />
          <Route path="go" element={<Go />} />
        </Route>
        <Route path="*" element={<div>  .</div>} />
      </Routes >
    </div>
  );
}

function About() {
  let navigate = useNavigate();

  return (
    <>
      <div className='About'>
        <h4>      .</h4>
        <p> ...</p>
        <div className='bbt'>
          <button className="bt bt1 bt2"
            onClick={() => {
              navigate("/about/pic");
            }}>picture</button>
          <button className="bt bt1 bt2" onClick={() => {
            navigate("/about/go");
          }}>Go Out</button>
        </div>
        <Outlet></Outlet>
      </div >
    </>
  )
}

export default App;
