import "../App.css";
import { useEffect, useState } from "react";

function Pic() {
  let [fade, setFade] = useState("");

  useEffect(() => {
    setTimeout(() => {
      setFade("end");
    }, 100);
    return () => {
      setFade("");
    };
  }, [Pic]);

  return (
    <div className={"start " + fade}>
      <div className="pic">
        <img className="img1" src={process.env.PUBLIC_URL + "/00.jpg"} />
        <img className="img1" src={process.env.PUBLIC_URL + "/01.jpg"} />
        <img className="img1" src={process.env.PUBLIC_URL + "/02.jpg"} />
        <img className="img1" src={process.env.PUBLIC_URL + "/03.jpg"} />
        <img className="img1" src={process.env.PUBLIC_URL + "/04.jpg"} />
        <img className="img1" src={process.env.PUBLIC_URL + "/05.jpg"} />
      </div>
    </div>
  );
}

export default Pic;
