import "../App.css";
import { useSelector, useDispatch } from "react-redux";
import { changeName, addCount, increase } from "../store";

function Cart() {
  let state = useSelector((state) => state);
  let dispatch = useDispatch();

  return (
    <div className="cart">
      <div className="tb tb-p">
        {state.user.name}({state.user.age}) 
      </div>
      <div>
        <button
          className="bt"
          onClick={() => {
            dispatch(changeName());
          }}
        >
          +++
        </button>
        <button
          className="bt"
          onClick={() => {
            dispatch(increase(1));
          }}
        >
          +++
        </button>
      </div>
      <table className="tb">
        <thead>
          <tr>
            <th>#</th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {state.cart.map((a, i) => (
            <tr key={i}>
              <td>{state.cart[i].id}</td>
              <td>{state.cart[i].name}</td>
              <td>{state.cart[i].count}</td>
              <td>
                <button
                  className="bt"
                  onClick={() => {
                    dispatch(addCount(state.cart[i].id));
                  }}
                >
                  +
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>{" "}
    </div>
  );
}

export default Cart;
