import "../App.css";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

function Card(props) {
  let [fade, setFade] = useState("");

  useEffect(() => {
    setTimeout(() => {
      setFade("end");
    }, 100);
    return () => {
      setFade("");
    };
  }, [Card]);
  let navigate = useNavigate();

  return (
    <div className="item">
      <img src={process.env.PUBLIC_URL + "/img" + props.items.id + ".jpg"} />
      <h4>{props.items.title}</h4>
      <p>{props.items.brand}</p>
      <p>{props.items.price} </p>
      <button
        className="bt"
        onClick={() => {
          navigate("/detail/" + props.items.id);
        }}
      >
        
      </button>
    </div>
  );
}

export default Card;
